<template>
	<div class="ProdCenterContainer moduleContainer">
		<div class="header">
			<div class="container">
				<div class="headerTabs">
					<div @click="changeTab(tab)" v-for="(tab,index) in tabsList" :key="index" :class="{'tabItem': true, 'active': currentTab == tab.type}">{{ tab.name }}</div>
				</div>
			</div>
		</div>
		<component :is="currentTab" />
		<FixedMenuVue />
	</div>
</template>

<script>
import FixedMenuVue from '@/components/FixedMenu/FixedMenu.vue';
import Speaker from "./Speaker/index.vue";
import CodePlate from "./CodePlate/index.vue";

export default {
	name: "ProdCenter",
	components: {
		FixedMenuVue,
		Speaker,
		CodePlate,
	},
	props: {},
	data() {
		return {
			tabsList: [
				{
					name: '云音箱',
					type: 'Speaker',
				},
				{
					name: '码牌',
					type: 'CodePlate',
				}
			],
			currentTab: 'Speaker',
		};
	},
	computed: {},
	watch: {},
	created() {},
	mounted() {},
	methods: {
		changeTab(tab) {
			this.currentTab = tab.type;
		},
	},
};
</script>
<style lang="scss" scoped>
@import "@/common/css/index.scss";

.ProdCenterContainer {
	.header {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		border-top: .0521vw /* 1px -> .0521vw */ solid #D3DAE5;
		// 在底部添加盒子阴影
		box-shadow: 0px .2604vw /* 5px -> .2604vw */ .3646vw /* 7px -> .3646vw */ -0.2604vw /* -5px -> -0.2604vw */ rgba(0, 0, 0, 0.5);
		z-index: 1;

		.headerTabs {
			display: flex;
			padding: 1.25vw /* 24px -> 1.25vw */ 0;

			.tabItem {
				padding: 0 1.25vw /* 24px -> 1.25vw */;
				border-right: .0521vw /* 1px -> .0521vw */ solid #EBEDF0;
				font-size: .7292vw /* 14px -> .7292vw */;
				cursor: pointer;

				&:last-child {
					border-right: none;
				}
			}

			.active {
				color: $mainColor;
			}
		}
	}
}
</style>
